
import { Events, NavController } from 'ionic-angular';
import { pageCommon } from '../../page.base';

/**
 * workflow 公共类
 */
export class workflowCommon extends pageCommon {

  constructor(public navCtrl: NavController,
    public events: Events, ) {

    super(navCtrl, events)
  }


  /**
   * 水平scroll寬度
   * @param width scroll 寬度
   * @param height scroll 高度
   */
  scrollWidth(width?:number,height?:number) {
    return super.scrollWidth(width, height)
  }

  /**
   * 發佈刷新完成事件
   * @param _event 刷新事件
   */
  completeRefresh(_event) {
    super.completeRefresh(_event)
  }
  /**
   * 返回上一頁
   */
  goBack() {
    this.events.publish('back-to-home')
  }

  /**
   * 打開任務詳細
   * @param item 任務對象
   */
  goDetails(item) {
    this.navCtrl.push('WorkflowDetailsPage', item)
  }
 
}



