import { Events } from 'ionic-angular';
import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { WorkflowProvider } from '../../../providers/workflow-provider';
import { ResponseObject } from '../../../models/models';
import { Session, StorageService, HttpService } from '../../../services/services';
import { UserProvider } from '../../../providers/providers';
import { workflowCommon } from '../workflow-tabs/workflow.common';

/**
 * Generated class for the WorkflowSettingPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-workflow-setting',
  templateUrl: 'workflow-setting.html',
})
export class WorkflowSettingPage extends workflowCommon{
  private ReceiveMsgFromWF: boolean
  private ShowMobileTaskOnly: boolean

  timer: any;


  constructor(public navCtrl: NavController,
    public navParams: NavParams,
    public workflowProvider: WorkflowProvider,
    public storageService: StorageService,
    public userProvider: UserProvider,
    public events: Events,
    public session: Session) {
      super(navCtrl, events);
      if(!this.session.setting){
        this.init().then(()=>{
          this.getSetting()
        })
      } else {
        this.getSetting()
      }

  }

  ionViewDidLoad() {
  }

  /**调用父类返回方法 */
  goBack() {
    super.goBack();
  }


  openMyAgent() {
    this.navCtrl.push('WorkflowSettingAgentlistPage')
  }
 

  getSetting(){
    this.ReceiveMsgFromWF = this.session.setting['RECEIVEMSGFROMWF'.toUpperCase()];
    this.ShowMobileTaskOnly = this.session.setting['SHOWMOBILETASKONLY'.toUpperCase()];
  }
  async init() { 
    this.userProvider.getPrivateSetting().subscribe(res => {
      let setting = {};
      for(let item of res.result){
        setting[item.name.toUpperCase()] = item.value;
      }
      this.session.setting = setting
    })
  }

  /**
   * 保存更改设置，定时为3s后保存，以免用户反复操作
   * @param type 更改设置类型：message为是否接收消息，mobile为是否只显示手机签核
   */
  changeSetting(type) {
    clearTimeout(this.timer)
    this.timer = setTimeout(()=>{
        let changeValue = {}
        if ('message' == type) {
          changeValue = { "name": "ReceiveMsgFromWF", "value": this.ReceiveMsgFromWF.toString() }
          
        } else if ('mobile' == type) {
          changeValue = { "name": "ShowMobileTaskOnly", "value": this.ShowMobileTaskOnly.toString() }
          this.session.setting['SHOWMOBILETASKONLY'.toUpperCase()] = this.ShowMobileTaskOnly
          this.events.publish('set-showmobiletask-success')
        }
    
        this.userProvider.privateSetting([changeValue]).subscribe((data: ResponseObject) => { 
        });
    },1500)
  }
    
 
}



